@media (max-width: 1414px) {
  .statsTable {
    margin-left: 1%;
  }
}

@media (max-width: 568px) {
  .flex-container {
    margin-top: 0.5em;
    display: block;
    width: 100%;
    height: auto;
    justify-content: center;
  }
  .typeButton,
  .selectedType {
    width: 90%;
    margin: 5px 5%;
  }
  .statsSelector {
    width: 90%;
    margin: 5px 5%;
  }
}
